.kasa-card {
  transition: all 0.2s ease-in-out;
}
.kasa-card:hover {
  transform: scale(1.025);
}
.kasa-card-content {
  background-color: #FF6060;
  border-radius: 1rem;
  height: 20rem;
  border-radius: 1rem;
  position: relative;
}
.kasa-card-content::after {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 1rem;
}
.kasa-card-content-img {
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 1rem;
  width: 100%;
  height: 100%;
}
.kasa-card-content-title {
  width: 80%;
  position: absolute;
  bottom: 5%;
  left: 5%;
  color: #ffffff;
  font-size: 4vw;
  font-weight: 400;
  z-index: 1;
}

@media screen and (min-width: 768px) {
  .kasa-card-content {
    height: 28rem;
    width: 28rem;
  }
  .kasa-card-content-title {
    font-size: 1.8rem;
  }
}
@media screen and (min-width: 1024px) {
  .kasa-card-content {
    height: 34rem;
    width: 34rem;
  }
}/*# sourceMappingURL=Card.css.map */