.kasa-tag {
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  background-color: #FF6060;
  text-align: center;
  font-size: 1rem;
  padding: 0.3rem 1.5rem;
  border-radius: 0.5rem;
  margin-right: 1rem;
}

@media screen and (min-width: 768px) {
  .kasa-tag {
    font-size: 1.2rem;
    padding: 0.4rem 1.75rem;
    border-radius: 1rem;
  }
}
@media screen and (min-width: 1024px) {
  .kasa-tag {
    font-size: 1.4rem;
    padding: 0.5rem 2rem;
    border-radius: 1rem;
  }
}/*# sourceMappingURL=Tag.css.map */