@import '../../styles/color.scss';

.kasa-navbar {
  margin: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &-logo {
    cursor: pointer;
    height: 10vw;
    max-height: 4.5rem;
    width: auto;
  }
  &-menu {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
    & a {
      display: flex;
      align-items: center;
      font-size: 1.2rem;
      font-weight: 500;
      color: $main-color;
      text-decoration: none;
      text-transform: uppercase;
      border-bottom: $white 0.2rem solid;
    }
    & .activeLink {
      border-color:  $main-color;
    }
  }
}

@media screen and (min-width: 768px){
  .kasa-navbar {
    margin: 3rem 5rem;
    &-logo {
      height: 8vw;
    }
    &-menu {
      gap: 2.5rem;
      & a {
        font-size: 1.8rem;
      }
    }
  }
}

@media screen and (min-width: 1024px){
  .kasa-navbar {
    margin: 4rem 10rem 2rem 10rem  ;
    &-logo {
      height: 6vw;
      max-height: 6.5rem;
    }
    &-menu {
      gap: 5rem;
      & a {
        font-size: 2.4rem;
        text-transform: none;
      }
    }
  }
}